const animatedScrollObservor = new IntersectionObserver(
    (entries, animatedScrollObservor)=>{
        entries.forEach((entry)=>{
            if (entry.isIntersecting) {
                entry.target.classList.add('enter')
                animatedScrollObservor.unobserve(entry.target)
            }
        })
    }
)
export default {
    bind(el){
        el.classList.add('before-enter')
        animatedScrollObservor.observe(el)
    }
}