<script>


export default {
  name: 'Main',
  components: {

  },mounted(){



    


const introduce = document.getElementById('introduce')
const name = document.getElementById('name')
const arrow = document.getElementById('arrow')

window.onscroll = ()=> scrollCheck()
scrollCheck()
function scrollCheck(){

      if (window.scrollY > 0) {
        arrow.style = '	animation: swing-out-top-bck 0.45s cubic-bezier(0.600, -0.280, 0.735, 0.045) both;'
      }else{
        arrow.style = '	animation: swing-in-top-fwd 0.5s cubic-bezier(0.175, 0.885, 0.320, 1.275) both;'

      }
      // console.log(window.innerHeight - window.scrollY)
      if (window.innerHeight - window.scrollY < 250 
      && window.innerHeight - window.scrollY > 50
      && window.innerWidth > 600){
        name.style = 'font-size:'+((window.innerHeight - window.scrollY)/50).toFixed(2)+1+'rem;'
      }else if(window.innerHeight - window.scrollY > 200) {
        name.style = ''
      }
      if (window.innerHeight - window.scrollY > (window.innerWidth > 600 ? 10 : 30)) {
        introduce.style = 'height:' + (window.innerHeight - window.scrollY) + 'px;'
        introduce.style.background ='#dfdfdf'
      }else{
        introduce.style.background = '#fff'  
      }

    }
  }
}
</script>
